@use 'base/variables' as v;
@use 'base/mixins' as m;

.bloques { 
    @include m.tablet { 
        margin-top: 7.5rem;
    }

    .bloque__contenido {
        padding: 2rem 0;
        @include m.desktop { 
            padding: 0;
        }
    
    }    
}

.bloques-1 {
    margin-top: 5rem;

    .bloque-bodega {
    
        &:nth-child(1) {
            background-color: darken($color: v.$primario, $amount: 10);
            grid-column: 2 / 3;
        }
        &:nth-child(2) {
            background-image: url(../img/bodegaBasic/whbodegabasic_hor.jpg);
        }
        &:nth-child(3) {
            background-color: v.$secundario;
            grid-column: 1 / 2;
        }
        &:nth-child(4) {
            background-image: url(../img/bodegaRefor/whbodegarefor_hor.jpg);
            background-position: center;
        }
    }
}

.bloques-2 {
    margin-top: 5rem;

    .bloque-oficina {
        
        &:nth-child(1) {
            background-color: darken($color: v.$primario, $amount: 10);
            grid-column: 2 / 3;
        }
        &:nth-child(2) {
            background-image: url(../img/oficinas/whoficina2_hor.jpeg);
        }
    }
}

.bloques-3 {
    margin-top: 5rem;

    .bloque-habitacional {
    
        &:nth-child(1) {
            background-color: darken($color: v.$primario, $amount: 10);
            grid-column: 1 / 2;
        }
        &:nth-child(2) {
            background-image: url(../img/habitacional/whhabitacional_ver.jpg);
        }
    }
}


.bloques-4 {
    margin-top: 5rem;

    .bloque-camarin {
    
        &:nth-child(1) {
            background-color: darken($color: v.$primario, $amount: 10);
            grid-column: 2 / 3;
        }
        &:nth-child(2) {
            background-image: url(../img/camarines/whcamarin_ver.jpg);
        }
    }
}
